.App{
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.Menu, .Quiz, .Endscreen{
  width: 500px;
  height: 500px;
  border-radius: 10px;
  background-color: lightseagreen;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
}

.Menu button, .Quiz button, .Endscreen button{
   width: 300px;
   height: 50px;
   margin: 5px;
   border: none;
   border-radius: 5px;
   font-size: 20px;
   cursor: pointer;
}

.options{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.options button:focus{background-color:#a29bfe;}

.next{
  margin-top: 500px;
}

 